  .App {
    text-align: center;
  }

  .App-logo {
    height: 40vmin;
    pointer-events: none;
  }

  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      animation: App-logo-spin infinite 20s linear;
    }
  }

  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }

  .App-link {
    color: #61dafb;
  }

  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }

  .No-Break-Line {
    white-space: nowrap;
    display: inline;
  }

  .MobileStyle {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .ZhuanP {
    margin: 5px;
  }

  .ZhuanLocationInput {
    width: 6rem;
    height: 2rem;
    font-size: 15px;
  }

  .CuzH2 {
    margin-bottom: 10px;
  }

  .ZhuanFuncButton {
    width: 10rem;
    height: 4rem;
    font-size: 15px;
  }